
/* text */
.bg-primary {
    background: $text-secondary; 
}
  
.bg-light {
    background: $bg-light; 
}

.text-primary {
    color:$text-primary; 
}

.text-secondary {
    color: $text-secondary; 
}

.text-lightDark {
    color: $text-lightDark; 
}

.text-light {
    color: $text-light; 
}


.d-none{
    display: none;
}

/* flex */
.d-flex {
    display: flex;
}

.flex-row-reverse {
    flex-direction: row-reverse;
}

.flex-column {
    flex-direction: column;
}

.flex-wrap {
    flex-wrap: wrap;
}

.justify-content-between {
    justify-content: space-between;
}

.justify-content-around {
    justify-content: space-around;
}

.justify-content-center {
    justify-content: center;
}

.justify-content-end {
    justify-content: flex-end;
}

.align-items-center {
    align-items: center;
}

.align-items-baseline {
    align-items: baseline;
}

.align-items-start {
    align-items: flex-start;
}

.align-items-end {
    align-items: flex-end;
}

.align-items-stretch {
    align-items: stretch;
}

.align-self-center {
    align-self: center;
}

.align-self-end {
    align-self: flex-end;
}

.h-100{
    height: 100%;
}

/* text */
.text-white {
    color: white;
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.text-left {
    text-align: left;
}

.text-justify {
    text-align: justify;
}

/* font-weight */
.font-weight-regular {
    font-weight: 400;
}

.font-weight-medium {
    font-weight: 500;
}

.font-weight-bold {
    font-weight: 700;
}

.font-weight-black {
    font-weight: 900;
}

.font-italic {
    font-style: italic;
}

/* padding */
.px-auto {
    padding-left: auto;
    padding-right: auto;
}

.py-auto {
    padding-top: auto;
    padding-bottom: auto;
}
