        center no-repeat;
    height: 840px;
}
/* classic */
.service li{
    width: 20%;
}

/* classic */
.classic .opticalImg {
    width: 635px;
    height: 320px;
    background: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-1.png") center center no-repeat;
    background-size: cover; 
