/* design */
.design {
    background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/bg.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; 
}

.design .double, .design .youth {
    width: 540px;
    height: 472px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; 
}
  
.design .double {
    background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-8.png"); 
}
  
.design .youth {
    background-image: url("https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/product-9.png");
}

/* recommand */
.recommand .cardContent{
    padding: 14px 16px 15px 16px;
    border: 1px solid #DEE2E6;
    border-top: 0;
}

.recommand li{
    max-width: 255px;
}

.indexContact .contactContainer {
    max-width: 730px;
    margin: 0 auto; 
}

.indexContact input[type="text"], .indexContact textarea {
    width: 100%;
    margin-top: 4px;
    margin-bottom: 16px;
    padding: 14px 16px;
    border: 1px solid #000;
    background-color: transparent; 
}
  
.indexContact textarea {
    resize: none;
    height: 136px; 
}
  
.indexContact input[type="checkbox"] + label {
    display: block;
    cursor: pointer;
    padding-left: 4px;
    margin-bottom: 42px; 
}
  
.indexContact input[type="checkbox"] {
    display: none; 
}
  
.indexContact input[type=checkbox] + label::before {
    content: "\2714";
    border: 1px solid #000;
    border-radius: 10%;
    display: inline-block;
    width: 24px;
    height: 24px;
    padding-left: 4px;
    margin-right: 8px;
    color: transparent;
    box-sizing: border-box;
    cursor: pointer; 
}
  
.indexContact input[type="checkbox"]:checked + label:before {
    background-color: #AA0601;
    border-color: #AA0601;
    color: #fff; 
}
  
.indexContact input[type="submit"] {
    background: #AA0601;
    padding: 16px 0;
    color: #ffffff;
    font-weight: 600;
    cursor: pointer;
    border: none;
    width: 350px;
    height: 54px; 
}
  
.indexContact input[type="submit"]:hover {
    background: #650300;
}
