$text-primary : #650300;
$text-secondary :#AA0601;
$text-lightDark:#212529;
$text-light:#707070;
$bg-light : #fbf2f2;



$font-sizes: 14 16 20 24 28 32 36 48 64 72;
$line-sizes: 21 24 30 36 54 72 90 96 108;

/* font-size */
@each $size in $font-sizes {
  .fz-#{$size} {
    font-size: #{$size}px;
  }
}

/* line-height size */
@each $size in $line-sizes {
  .lh-#{$size} {
    line-height: #{$size}px;
  }
}

/* margin padding */
$properties: (
  "mt": margin-top,
  "mr": margin-right,
  "mb": margin-bottom,
  "ml": margin-left,
  "pt": padding-top,
  "pr": padding-right,
  "pb": padding-bottom,
  "pl": padding-left 
);

@each $property, $property-value in $properties {
  @for $i from 1 through 11 {
    .#{$property}-#{$i * 8} {
      #{$property-value}: 8px * $i;
    }
  }
}

$mt-sizes: 0 4 7 10 20 30 36 42 46 60 304;
$mr-sizes: 0 4 7 10 20 30 36 42 46 60 304;
$mb-sizes: 0 4 7 10 20 30 36 42 46 60 304;
$ml-sizes: 0 4 7 10 20 30 36 42 46 60 304;
$pt-sizes: 0 4 7 10 20 30 36 42 46 60 304;
$pr-sizes: 0 4 7 10 20 30 36 42 46 60 304;
$pb-sizes: 0 4 7 10 20 30 36 42 46 60 304;
$pl-sizes: 0 4 7 10 20 30 36 42 46 60 304;

@each $property, $property-value in $properties {
  @if $property == mt {
    @each $size in $mt-sizes {
      .#{$property}-#{$size} {
          #{$property-value}: #{$size}px;
      }
    }
  }
  @else if $property == mr {
    @each $size in $mr-sizes {
      .#{$property}-#{$size} {
        #{$property-value}: #{$size}px;
      }  
    }
  } 
  @else if $property == mb {
    @each $size in $mb-sizes {
      .#{$property}-#{$size} {
        #{$property-value}: #{$size}px;
      }  
    }
  }
  @else if $property == ml {
    @each $size in $ml-sizes {
      .#{$property}-#{$size} {
        #{$property-value}: #{$size}px;
      }  
    }
  }
  @else if $property == pt {
    @each $size in $pt-sizes {
      .#{$property}-#{$size} {
        #{$property-value}: #{$size}px;
      }  
    }
  }
  @else if $property == pr {
    @each $size in $pr-sizes {
      .#{$property}-#{$size} {
        #{$property-value}: #{$size}px;
      }  
    }
  }
  @else if $property == pb {
    @each $size in $pb-sizes {
      .#{$property}-#{$size} {
        #{$property-value}: #{$size}px;
      }  
    }
  }
  @else if $property == pl {
    @each $size in $pl-sizes {
      .#{$property}-#{$size} {
        #{$property-value}: #{$size}px;
      }  
    }
  }
}