
/* footer */
.footer {
  background-color: #AA0601;
  padding-top: 48px;
  padding-bottom: 28px;
}

.footer .container:first-child {  
  border-bottom: 1px solid #fff;
}

.footer .menu li {
  margin-right: 48px;
}

.footer .applink li:not(:first-child) {
  margin-left: 32px;
}

.footer .menu li a {
  padding: 12px 0;
}

.footer .phone, .footer .mail {
  font-size: 36px;
}

.footer .phone {
  margin-top: 18px;
}

.footer .mail {
  margin-top: 17px;
  padding-bottom: 20px;
}

.footer .phone img {
  height: 44px;
  margin-right: 17px;
}

.footer .mail img {
  margin-right: 12px;
}

.footer .copyright {
  width: 100%;
  margin-top: 24px;
}


.footer .copyright .logo a {
  margin-right: 38px;
}

.footer .copyright ul {
  font-size: 14px;
}

.footer .copyright ul li {
  margin-left: 62px;
}



